import React from 'react'

import { render, fireEvent } from '@testing-library/react-native'

import { Address } from '../Address'

describe('Address component', () => {
  it('should copy full address to clipboard and change opacity on press', () => {
    const rlyAccount = 'exampleAddress12345'

    const { getByText, getByTestId } = render(
      <Address rlyAccount={rlyAccount} />,
    )

    const addressText = getByText('exampl...12345')
    const copyButton = getByTestId('copy-button')

    // Проверяем начальное значение опасити
    expect(addressText.props.style[0].opacity).toBe(1)

    // Нажимаем на кнопку копирования
    fireEvent.press(copyButton)

    // Проверяем изменение опасити
    expect(addressText.props.style[0].opacity).toBe(0.5)

    // Ждем 1 секунду (период изменения опасити)
    setTimeout(() => {
      // Проверяем возвращение опасити к исходному значению
      expect(addressText.props.style[0].opacity).toBe(1)
    }, 300)
  })
})
